import React from "react";

export default class StayLoveSectionComponent extends React.Component {
    constructor(props) {
        super(props);
    }
    doContinue(){
        this.props.authorizationSuccess(true,null);
    }
    render() {
        return (
            <div>
                <h5>Czy chcesz przy nich zostać?</h5>
                <p>Jeśli tak - przeniesiemy Cię na stronę z ofertą Love dla Firm.</p>

                <div className={"l-row u-padding-top-l"}>
                    <div className={"l-col l-col-6 l-col-small-12 u-padding-top-l"}>
                        <button id="continue" onClick={this.doContinue.bind(this)}
                                className="o-btn opl-btn opl-btn--secondary u-w-100">  Nie, chcę przedłużyć sam abonament</button>
                    </div>
                    <div className={"l-col l-col-6 l-col-small-12 u-padding-top-l"}>
            <button id="stay" onClick={this.props.stayLove}
                                    className="o-btn opl-btn opl-btn--primary u-w-100 u-padding-top-l u-padding-l ">Tak, chcę zostać w Love dla Firm</button>
                    </div>
                </div>
            </div>
        );
    }
}